@import url(https://fonts.googleapis.com/css2?family=Open+Sans+Condensed:wght@300&family=Open+Sans:wght@300;400;600&family=Playfair+Display&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

html,body,div,span,applet,object,iframe,h1,h2,h3,h4,h5,h6,p,blockquote,pre,a,abbr,acronym,address,big,cite,code,del,dfn,em,img,ins,kbd,q,s,samp,small,strike,strong,sub,sup,tt,var,b,u,i,center,dl,dt,dd,ol,ul,li,fieldset,form,label,legend,table,caption,tbody,tfoot,thead,tr,th,td,article,aside,canvas,details,embed,figure,figcaption,footer,header,hgroup,menu,nav,output,ruby,section,summary,time,mark,audio,video{margin:0;padding:0;border:0;font-size:100%;font:inherit;vertical-align:baseline}article,aside,details,figcaption,figure,footer,header,hgroup,menu,nav,section{display:block}body{line-height:1}ol,ul{list-style:none}blockquote,q{quotes:none}blockquote:before,blockquote:after,q:before,q:after{content:'';content:none}table{border-collapse:collapse;border-spacing:0}button{border:none;background:none;cursor:pointer}button:focus{outline:none}body{font-family:"Open Sans","Playfair Display",Arial,Helvetica,sans-serif;font-weight:300}h1{font-size:2.75rem;text-indent:2rem;font-family:"Playfair Display","Open Sans",Arial,Helvetica,sans-serif;line-height:.55;position:relative;left:1rem;padding-bottom:1rem}h2{font-weight:400}h2,h3{text-align:center}h2{font-size:1.125rem}button{font-family:"Open Sans","Playfair Display",Arial,Helvetica,sans-serif;font-weight:300}button:hover,button:focus{color:#d53f17;font-weight:400}.heading{text-transform:uppercase;font-size:0.75rem;letter-spacing:2px;color:#ec6e4c}.forecast__description{text-transform:capitalize;text-align:center;padding-bottom:.5rem;font-size:0.875rem;color:#d53f17}.forecast__details{font-size:0.875rem}.details{text-transform:uppercase;color:#d53f17;letter-spacing:-.5px;font-size:0.9375rem}.forecast__temp--main{text-align:center;font-size:1.125rem;font-weight:400}.forecast__temp--min{padding-right:1rem;color:#4a77a1}.forecast__temp--max{color:#ec6e4c}.forecast__temp--min,.forecast__temp--max{font-size:0.875rem}.units{color:rgba(72,72,74,0.85);font-size:0.8125rem}.units input[type="radio"]:checked+label{color:#e7491e;font-weight:400}ul{display:flex;flex-wrap:wrap;justify-content:center}.buttons{padding-top:1rem}.buttons__hours{padding-bottom:.5rem}.units{position:absolute;bottom:.75rem;left:50%;transform:translateX(-50%)}.units input[type="radio"]{position:fixed;opacity:0;pointer-events:none}.units label{cursor:pointer}.forecast__heading{padding:1rem 0}.forecast__main{display:flex;justify-content:space-around;align-items:center;padding-right:1rem}.forecast__temp{display:flex;flex-direction:column}.App{width:100vw;max-width:100%;height:100vh;max-height:100%;display:flex;align-items:center;justify-content:center}.content{width:90vw;max-width:800px;min-width:250px;height:90vh;max-height:500px;display:grid;grid-template-columns:repeat(1, 1fr);border:1px solid #48484a}.main{position:relative;padding:2rem;grid-column:1;min-width:250px;display:flex;flex-direction:column;justify-content:center}.image{grid-column:2/-1}.image{height:100%;width:100%;overflow:hidden;display:none}@media all and (min-width: 576px){.image{display:block}}.image>img{height:100%;width:100%;object-fit:cover}

